html {
  box-sizing: border-box;
  font-size: 18px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

p {
  margin: 0;
  padding: 0;
}

.btn {
  padding: 5px 10px;
  border: none;
  outline: none;
  border-radius: 3px;
  cursor: pointer;
}

.modal {
  opacity: 0;
  z-index: -1;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0);
  transform: scale(0);
  transition: all 0.3s ease-in;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-window {
  width: 600px;
  border-radius: 5px;
  background-color: #fff;
  padding: 20px 10px;
  transform: translateY(-200%);
  transition: all 0.5s ease-in;
  opacity: 0;
}

.modal--open {
  opacity: 1;
  z-index: 200;
}

.modal--hide {
  opacity: 1;
  z-index: 200;
}

.modal--open .modal-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  transform: scale(1);
}

.modal--open .modal-window {
  opacity: 1;
  transform: translateY(0);
}

.modal--hide .modal-overlay {
  background-color: rgba(0, 0, 0, 0);
  transform: scale(0);
}

.modal--hide .modal-window {
  opacity: 0;
  transform: translateY(-200%);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
}

.modal-header__close {
  cursor: pointer;
}

.modal-body {
  padding: 20px 0 20px 10px;
}

.modal-footer {
  text-align: right;
}
